.wrap-h {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;

    .container {
        padding-top: 70px;
        width: 90%;
        height: 100%;
        // background: #ccc;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        align-items: center;

        .container-left {
            width: 75%;
            height: 100%;

            // background: gray;
            .c-l-tip {
                width: 100%;
                // height: 250px;
                background: #ddd;
                margin-top: 20px;
                border-radius: 10px;
                padding: 20px;
                box-shadow: 3px 3px 5px #ccc;
                cursor: pointer;

                .header {
                    font-size: 25px;
                    // font-weight: 800;
                }

                .introduce {
                    padding-top: 10px;
                    width: 100%;
                    display: flex;
                    justify-content: flex-start;
                    align-items: center;

                    span {
                        font-size: 14px;
                        padding-right: 10px;
                    }
                }

                .content {
                    padding-top: 20px;
                    text-indent: 2em;
                    color: #555;
                    line-height: 25px;
                }
            }
        }

        .container-right-h {
            width: 24%;
            height: 600px;
            background: #fff;
            border: 2px solid #ccc;
            margin-top: 40px;
            border-radius: 10px;
            box-shadow: 1px 1px 5px #ccc;
        }
    }
}